<template>
 <div class="zipkinSaved">
    <el-container  class="detailZipkin">
        <div class="button-group">
            <span class="route-name">{{$route.meta.title}}</span>
        </div>
        <el-header style="width:100%;height:100%">
            <el-form ref="addForm" :model="addForm" status-icon label-width="0px" size="small">
                <el-form-item>
                    <el-row :gutter="10">
                        <el-col :span="12" style="position:relative">
                            <el-col :span="19">
                                <el-input v-model="addForm.savePath" placeholder="请选择文件(.txt/.json)"></el-input>
                            </el-col>
                            <el-col :span="5">
                                <input type="file" class="input-file" @change="handleChange($event)" accept="text/plain, application/json"/>
                                <el-button type="primary" size="small">{{$t('common.viewFile')}}</el-button>
                            </el-col>
                        </el-col>
                    </el-row>
                </el-form-item>
            </el-form>
            <div v-if="tableVisible" key="zipkinHeader">
                <el-row  type="flex" justify="middle" class="clearfix">
                    <el-col class="header-lf" :span="2">
                        <el-tooltip class="item" effect="dark" content="调用链总耗时" placement="top">
                            <el-button>{{$t('zipkin.duration')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag class="button-new-tag" size="mini">{{detailForm.lookback}}</el-tag>
                    </el-col>
                    <el-col class="header-lf" :span="2">
                        <el-tooltip class="item" effect="dark" content="涉及的服务总数" placement="top">
                            <el-button>{{$t('zipkin.serviceNum')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag class="button-new-tag" size="mini">{{detailForm.seviceNum}}</el-tag>
                    </el-col>
                    <el-col class="header-lf" :span="2">
                        <el-tooltip class="item" effect="dark" content="调用的深度" placement="top">
                            <el-button>{{$t('zipkin.depth')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag class="button-new-tag" size="mini">{{detailForm.depth}}</el-tag>
                    </el-col>
                    <el-col class="header-lf" :span="2" style="min-width:83px;">
                        <el-tooltip class="item" effect="dark" content="调用spansd的总数量" placement="top">
                            <el-button>{{$t('zipkin.spansNum')+'：'}}</el-button>
                        </el-tooltip>
                    </el-col>
                    <el-col class="header-rt" :span="2">
                        <el-tag  class="button-new-tag" size="mini">{{detailForm.spanNum}}</el-tag>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="4">
                        <div class="block">
                            <el-cascader
                                size="mini"
                                placeholder="搜索"
                                :options="options"
                                @change="handleItemChange"
                                filterable
                                clearable
                            ></el-cascader>
                        </div>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <div v-if="savedData.serviceCounts && savedData.serviceCounts.length > 0">
                            <span class="zipList" v-for="item in savedData.serviceCounts" :key="item.id" style="margin-right:10px">
                                {{item.name + ' x' + item.count + ' ' + item.max + 'ms'}}
                            </span>
                        </div>
                    </el-col>
                </el-row>
            </div>
        </el-header>
         <!-- 表格部分 -->
        <el-main v-if="tableVisible">
            <el-row v-if="savedData.timeMarkers && savedData.timeMarkers.length > 0">
                <el-col :span="4">
                    <el-tag size="small" style="margin-left:10px">{{$t('zipkin.serviceName')}}</el-tag>
                </el-col>
                <el-col v-for="item in savedData.timeMarkers" :span="4" :key="item.id" align="right">
                    <el-tag size="small" v-if="item.time">{{item.time}}</el-tag>
                </el-col>
            </el-row>
            <el-row class="main-box" v-for="item in mainData" :key="item.id" v-if="item.spreadStatus">
                <div @click="checkHandle(item)" style="width:100%;height:23px">
                    <el-col class="main-title" :span="4">
                        <div class="main-title-box" :style="{'margin-left':item.depthClass*10 + 5 + 'px'}">
                            <el-button @click="spreadHandle($event,item)" class="main-add" v-if="item.children === '' ? false :true">{{item.spread ? '-' :'+'}}</el-button>
                            <el-tooltip :content="item.serviceName" placement="top" >
                                <el-button size="mini" >{{item | shortWords}}</el-button>
                            </el-tooltip>
                        </div>
                    </el-col>
                    <el-col class="main-content" :span="20" style="position:relative">
                        <el-button size="mini" :style="{'position':'absolute','left':item.left+ '%'}">{{item.durationStr +' '+item.spanName}}</el-button>
                    </el-col>
                </div>
            </el-row>
        </el-main>
        <!-- 弹出框 -->
        <el-dialog class="diglogBox" :visible.sync="dialogVisible" width="60%">
            <span slot="title" class="dialog-title">
                <span>{{diglogTitle}}</span>
            </span>
            <span class="service">{{$t('zipkin.serviceName')}} ： {{ dialogTable.serviceName}}</span>
            <el-table
                stripe
                :data="dialogTable.annotations"
                ref="multipleTable"
                style="width: 100%;margin-top:24px">
                    <el-table-column
                        align="center"
                        :label="$t('zipkin.dateTime')">
                        <template slot-scope="props">
                            <span class="zipList">{{props.row.timestamp | timeFilter(that)}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column
                        align="center"
                        width="150px"
                        prop="relativeTime"
                        :label="$t('zipkin.relativeTime')">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        width="200px"
                        prop="value"
                        :label="$t('zipkin.annotation')">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="endpoint"
                        :label="$t('zipkin.address')">
                    </el-table-column>
            </el-table>
            <el-table
                stripe
                :data="dialogTable.binaryAnnotations"
                ref="multipleTable"
                style="width: 100%;margin-top:24px">
                    <el-table-column
                        align="center"
                        prop="key"
                        :label="$t('zipkin.key')">
                    </el-table-column>
                    <el-table-column
                        align="center"
                        prop="value"
                        :label="$t('zipkin.value')">
                    </el-table-column>
            </el-table>
            <el-collapse accordion>
                <el-collapse-item :title="$t('zipkin.moreInfo')">
                    <table class="table">
                        <tbody>
                            <tr>
                                <td class="key" data-key="key">traceId</td>
                                <td class="value" data-key="value">{{savedData.traceId}}</td>
                            </tr>
                            <tr>
                                <td class="key" data-key="key">spanId</td>
                                <td class="value" data-key="value">{{dialogTable.spanId}}</td>
                            </tr>
                            <tr>
                                <td class="key" data-key="key">parentId</td>
                                <td class="value" data-key="value">{{dialogTable.parentId}}</td>
                            </tr>
                        </tbody>
                    </table>
                </el-collapse-item>
            </el-collapse>
        </el-dialog>
    </el-container>
 </div>
</template>

<script type="text/ecmascript-6">
import {SPAN_V1} from '@/utils/spanConverter.js'
import skew from '@/utils/skew.js'
import traceToMustache from '@/utils/traceToMustache'
export default {
    name: 'zipkinSaved',
    data() {
        return {
            addForm: {
                savePath: '', // 保存路径
                Describe: '' // 描述
            },
            that: this,
            options: [],
            detailForm: {
                lookback: '',
                seviceNum: '',
                depth: '',
                spanNum: ''
            },
            savedData: {}, // 总数据
            mainData: { // 主体数据
                serviceCounts: []
            },
            tableVisible: false, // 主体内容
            dialogVisible: false, // 弹出框
            diglogTitle: '', // detail弹出框标题
            dialogTable: {
                serviceName: '',
                annotations: [],
                binaryAnnotations: [],
                spanId: '',
                parentId: ''
            }
        }
    },
    computed: {
    },
    watch: {
    },
    mounted() {
    },
    methods: {
        // 浏览文件
        handleChange(event) {
            this.options = []
            this.detailForm = {
                lookback: '',
                seviceNum: '',
                depth: '',
                spanNum: ''
            }
            this.savedData = {}
            this.mainData = {
                serviceCounts: []
            }
            const _this = this
            let files = event.target.files[0]
            const reader = new FileReader()
            reader.onload = evt => {
                // console.log(evt)
                try {
                    const rawTrace = JSON.parse(evt.target.result)
                    const v1Trace = rawTrace.map(SPAN_V1.convert)
                    const mergedTrace = SPAN_V1.mergeById(v1Trace)
                    const clockSkewCorrectedTrace = skew.correctForClockSkew(mergedTrace)
                    const modelview = traceToMustache(clockSkewCorrectedTrace, '')
                    _this.tableVisible = true
                    _this.addForm.savePath = event.target.value
                    _this.detailForm = {
                        lookback: modelview.duration,
                        seviceNum: modelview.services,
                        depth: modelview.depth,
                        spanNum: modelview.totalSpans
                    }
                    Object.keys(modelview.spans).forEach((value) => {
                        modelview.spans[value].spread = true
                        modelview.spans[value].spreadStatus = true
                    })
                    _this.savedData = JSON.parse(JSON.stringify(modelview))
                    _this.mainData = modelview.spans
                    _this.savedData.serviceCounts.forEach((m) => {
                        _this.options.push({
                            value: m.name,
                            label: m.name
                        })
                    })
                } catch (a) {
                    _this.$message({
                        type: 'warning',
                        message: '数据错误或者格式不对'
                    })
                }
            }
            setTimeout(() => reader.readAsText(files), 0)
        },
        // 查看详情
        checkHandle(item) {
            this.diglogTitle = item.spanName + ':' + item.durationStr
            this.dialogTable = {
                serviceName: item.serviceName,
                annotations: item.annotations,
                binaryAnnotations: item.binaryAnnotations,
                spanId: item.spanId,
                parentId: item.parentId
            }
            this.dialogVisible = true
        },
        // 搜索
        handleItemChange(val) {
            let ck = JSON.parse(JSON.stringify(this.savedData.spans))
            if (!val.length > 0) {
                this.mainData = ck
            } else {
                let arr = []
                ck.forEach((a) => {
                    val.forEach((b) => {
                        if (b === a.serviceName) {
                            arr.push(a)
                        }
                    })
                })
                this.mainData = arr
            }
        },
        // 折叠展开
       spreadHandle(ev, item) {
            ev.stopPropagation()
            let id = this.mainData.indexOf(item)
            if (item.spread === false) {
                this.$set(this.mainData[id], 'spread', true)
                for (let index = id + 1; index < this.mainData.length; index++) {
                    if (this.mainData[index].depthClass > this.mainData[id].depthClass) {
                        this.mainData[index].spread = true
                        this.mainData[index].spreadStatus = true
                    } else {
                        break
                    }
                }
            } else {
                this.$set(this.mainData[id], 'spread', false)
                for (let index = id + 1; index < this.mainData.length; index++) {
                    if (this.mainData[index].depthClass > this.mainData[id].depthClass) {
                        this.mainData[index].spreadStatus = false
                    } else {
                        break
                    }
                }
            }
       }
    },
    filters: {
        timeFilter(value, that) {
            let va = that.$moment(Math.trunc(value / 1000)).format('YYYY-MM-DD hh:mm:ss a')
            return va
        },
        shortWords(item) {
            let value = item.serviceName
            if (value) {
                if (value.length > 16) {
                    return value.slice(0, 15) + '...'
                } else {
                    return value
                }
            } else {
                return item.depthClass + '-' + value
            }
        }
    }
}
</script>

<style scoped lang="scss">
.input-file {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
}
.detailZipkin{
    .el-header{
        background: #f5f5f5;padding:10px 10px;
        .el-col{
            height: 40px;line-height: 40px
        }
        .header-lf{
            .el-button{
                border: none;padding:0px;
                background: #f5f5f5;
                font-weight: 600;
            }
        }
        .header-rt{
            .el-tag{
                border: none;background: #428bca;
                color: #ffffff;
            }
        }
        .header-js{
            .el-button{
                border: none;background: #428bca;
                color: #ffffff;
            }
        }
        .zipList{
            display: inline-block;
            padding: .25em .4em;
            font-size: 75%;
            font-weight: 700;
            line-height: 1;
            text-align: center;
            white-space: nowrap;
            vertical-align: baseline;
            border-radius: .25rem;
            color: #fff;
            background-color: #28a745;
        }
    }
    .el-main{
        font-weight: 400;
        line-height: 23px;
        color: #212529;
        text-align: left;
        font-size: 14px;
        .el-tag{
            background: #ffffff;
            border:none;
        }
        .el-button{
            border:none;
        }
        .main-box{
            cursor: pointer;height: 34px;line-height: 34px;
            .el-button{
               color: #000000; color: #000000;
            }
            &:hover{
                background: #eee;border-color: #ccc;
            }
            .main-title{
                .main-title-box{
                    position: relative;
                    .main-add{
                        padding:7px 3px;
                        position: absolute;left:-5px;top:3px;
                        width: 14px;line-height: 14px;background: none
                    }
                    .el-tooltip{
                        padding:7px 3px;margin-left: 10px;
                        background: #0084b4;color:#ffffff;
                    }
                }
            }
            .main-content{
                padding: 3px;
                .el-button{
                    background: #a9c9DE;
                }
                span{
                    white-space:nowrap;
                }
            }
        }
    }
    .table{
        width: 100%;
        margin-bottom: 1rem;background-color: transparent;
        border-collapse: collapse;border-spacing: 2px;
        tbody{
            display: table-row-group;
            td{
               border: 1px solid #ebeef5;text-align: center;
               line-height: 23px;padding:12px 0;
            }
        }
    }
    .diglogBox{
        .dialog-title{
            font-size: 22px;font-weight: 600;color: #000000;
        }
        .service{
            line-height: 24px;
            font-size: 16px;
            color: #303133;
        }
    }
}
</style>



